.new-minecraft {
  .minecraft-edu {
    padding: 20px 0;
    @media screen and (min-width: 768px) {
      padding: 60px 0;
    }
    & .text {
      line-height: 1.3;
      font-size: 14px;
      margin-bottom: 20px;
      & > span {
        font-weight: 500;
      }
      @media screen and (min-width: 768px) {
        font-size: 18px;
        margin-bottom: 40px;
      }
      @media screen and (min-width: 1280px) {
        max-width: 800px;
        font-size: 20px;
        margin-bottom: 12px;
      }
    }
    &_info-wrapper {
      background-color: $color-nm-yellow;
      display: flex;
      flex-direction: column;
      padding: 40px 16px;
      margin-bottom: 20px;
      align-items: center;
      gap: 20px;
      border-radius: 4px;

      & > .box-content {
        @media screen and (min-width: 768px) {
          max-width: 319px;
        }
        @media screen and (min-width: 1280px) {
          max-width: 558px;
        }
        & > h3 {
          width: 100%;
          font-weight: 500;
          line-height: 1.2;
          font-size: 20px;
          margin-bottom: 20px;
          @media screen and (min-width: 1280px) {
            margin-bottom: 24px;
            font-size: 24px;
          }
        }
        & > p {
          margin-bottom: 20px;
          font-size: 16px;
          line-height: 1.2;
          @media screen and (min-width: 768px) {
            margin-bottom: 24px;
          }
        }
      }
      @media screen and (min-width: 768px) {
        flex-direction: row;
        padding: 40px 17px;
        margin-bottom: 40px;
        gap: 32px;
      }
      @media screen and (min-width: 1280px) {
        padding: 20px 24px;
      }
    }
    &_box {
      background-color: $color-white;
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 40px;
      gap: 20px;
      .box-content {
        @media screen and (min-width: 768px) {
          max-width: 336px;
        }
        @media screen and (min-width: 1280px) {
          max-width: 448px;
        }
        &_subtitle {
          text-align: start;
          font-weight: 500;
          line-height: 1.2;
          font-size: 20px;
          margin-bottom: 20px;
          @media screen and (min-width: 1280px) {
            margin-bottom: 24px;
            font-size: 24px;
            text-align: start;
          }
        }
        &_text {
          margin-bottom: 20px;
          font-size: 16px;
          line-height: 1.2;
          @media screen and (min-width: 768px) {
            margin-bottom: 24px;
          }
          @media screen and (min-width: 1280px) {
            font-size: 20px;
          }
        }
      }
      @media screen and (min-width: 768px) {
        flex-direction: row;
        margin-bottom: 40px;
        justify-content: space-around;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
    &_item {
      position: relative;
      font-size: 16px;
      padding-left: 30px;
      line-height: 1.2;
      margin-bottom: 8px;
      @media screen and (min-width: 1280px) {
        margin-bottom: 12px;
        font-size: 20px;
        &--objects {
          max-width: 320px;
        }
      }
      &:before {
        position: absolute;
        content: url("../images/new-minecraft/li-cub.svg");
        width: 21px;
        height: 24px;
        top: 0;
        left: 0;
      }
    }
  }
}
